.container{display:flex;flex-wrap:wrap;width:90%;margin:0 auto}@media all and (max-width: 3000px){.container .imgContainer{width:calc(33% - 2rem)}.button{display:block;margin:.4em auto}}@media all and (max-width: 1750px){.container .imgContainer{width:calc(50% - 2rem)}.container .imgContainer img{max-height:200px}}@media all and (max-width: 1000px){.container .imgContainer{width:100%}.container.imgContainer img{max-height:200px}}.container .imgContainer{max-width:500px;max-height:600px;margin:1rem;border:5px solid #000;border-radius:10px;display:flex;justify-content:space-between;flex-direction:column;position:relative;align-items:center}.container .imgContainer img{width:300px;max-height:300px;align-items:center;transition:transform 450ms}.container .imgContainer img:hover{transform:scale(1.12)}.button{display:inline-block;border-radius:99px;background-color:#1db954;border:none;color:#fff;text-align:center;align-items:center;font-size:20px;padding:15px;width:150px;transition:all .5s;margin:20px;text-decoration:none;font-weight:600}.button-container{display:flex;flex-wrap:wrap}.button span{display:inline-block;position:relative;transition:.5s}.button span:after{content:"»";position:absolute;opacity:0;top:0;right:-20px;transition:.5s}.button:hover span{padding-right:25px}.button:hover span:after{opacity:1;right:0}.container .imgContainer h4{color:#fff;text-align:center;font-size:26px}.imgContainer p{color:#fff;text-align:center;font-size:20px}